.descriptionBlock{
    display: flex;
    padding: 10px;
    font-size: 30px
}

.descriptionBlock img{
    margin-right: 10px
}

.mainPhoto{
    max-width: 200px;
}

.touchBlock{
    position: relative;
}

.spanBlock{
    display: none;
    position: absolute;
    top: 0;
    left: 0;
    color: #B22222;
    padding: 0 15px 0 14.6px;
    transition: .5s;
    font-size: 19px;
}

.spanBlock:hover{
    color: #000;
    background-color:wheat;
}

.touchBlock:hover .spanBlock{
    display: block;
}

.infoBlock b{
    font-size: 22px;
}

.myContacts{
    margin-left: 10px;
}

.toggleEditMode{
    display: none;
}

.infoBlock:hover .toggleEditMode{
    display: block;
}